<template>
  <div class="container bg-container">
    <van-nav-bar
      left-arrow
      title="领取记录"
      @click-left="$goBack" />
    <div class="list">
      <div class="item" v-for="item in records">
        <div class="flex row">
          <div class="label">分红数量：</div>
          <div class="flex-item cont">{{item.amount}} BUSD</div>
        </div>
        <div class="flex row">
          <div class="label">领取时间：</div>
          <div class="flex-item cont">{{item.time}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import { Toast } from 'vant';
import { reactive, toRefs, computed, watch} from "vue";
import freezeAbi from "@/utils/freezeAbi";
import tokenAbi from "@/utils/abi";
import config from "@/utils/config";
import {useStore} from "vuex";
import Dateformat from "dateformat";
export default {
  setup () {
    const store = useStore()
    const data = reactive({
      records: [],
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      freezeContract: null,
      BusdContract: null,
      initContract: () => {
        data.freezeAbi = new data.web3.eth.Contract(freezeAbi, config.frozen)
        data.BusdContract = new data.web3.eth.Contract(tokenAbi, config.BusdAddress)
        data.initData()
      },
      initData: () => {
        data.getList()
      },
      getList: async () => {
       let decimals = await data.BusdContract.methods.decimals().call();
       let result =  await data.freezeAbi.getPastEvents('RewardPaid', {
            filter: {_user: [data.address]}, // Using an array means OR: e.g. 20 or 23
            fromBlock: 0,
            toBlock: 'latest'
        })
        console.log(result)
        let arr = [];
        result.forEach((item) => {
          console.log(item)
          let params = {
            time: Dateformat(new Date(item.returnValues._time * 1000), "yyyy-mm-dd hh:MM"),
            amount: Number(item.returnValues._reward / Math.pow(10, decimals)).toFixed(2)
          }
          arr.push(params)
        })
        data.records = arr;
        console.log(data.records)
      }
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

<style lang="less" scoped>
.list {
  margin: 15px;
  border-radius: 25px;
  background: #fff;
  padding: 20px 15px;
  .item {
    background: #EEEAF4;
    border-radius: 13px;
    padding: 13px 12px;
    color: #8F80BA;
    .row + .row {
      margin: 6px 0 0;
    }
    .cont {
      word-break: break-all;
    }
    .num {
      font-size: 15px;
      font-weight: bold;
    }
  }
  .item + .item {
    margin-top: 15px;
  }
}
</style>
